class BikeSettings {
    constructor(bikeWeight, wheelsInertia, wheelsCircumference, efficiency, area, cx, coefficientsFile) {
        this._bikeWeight = bikeWeight;
        this._wheelsInertia = wheelsInertia;
        this._wheelsCircumference = wheelsCircumference;
        this._efficiency = efficiency;
        this._area = area;
        this._cx = cx;
        this._coefficientsFile = coefficientsFile;
    }
    get bikeWeight() {
        return this._bikeWeight;
    }
    get wheelsInertia() {
        return this._wheelsInertia;
    }
    get wheelsCircumference() {
        return this._wheelsCircumference;
    }
    get wheelsRadius() {
        return this._wheelsCircumference / (2 * 3.1415);
    }
    get efficiency() {
        return this._efficiency;
    }
    get area() {
        return this._area;
    }
    get cx() {
        return this._cx;
    }
    get coefficientsFile() {
        return this._coefficientsFile;
    }
}
class UserSettings {
    constructor(riderWeight, riderHeight, rho) {
        this._riderWeight = riderWeight;
        this._riderHeight = riderHeight;
        this._rho = rho;
    }
    get riderWeight() {
        return this._riderWeight;
    }
    get riderHeight() {
        return this._riderHeight;
    }
    get rho() {
        return this._rho;
    }
}
class Bike {
    constructor(bikeName, imgSrc, description, settings) {
        this.bikeName = bikeName;
        this.imgSrc = imgSrc;
        this.description = description;
        this.settings = settings;
    }
}
export { Bike, BikeSettings, UserSettings };
